<template>
 <div class="course-construction">
     <title-curmb></title-curmb>
     <course-steps :title="courseName" :step="5"></course-steps>
     <div class="course-construction_body">
         <el-form :model="form" :rules="rules" ref="form" label-width="0">
              <el-form-item  prop="tableData">
             <el-row v-for="(item, index) in tableData" :key="index">
                 <h2>{{`“${item.examine_name}”考核与评价标准`}}</h2>
                        <el-table :data="item.requires" border class="cc-table" style="width: 100%;">
                            <el-table-column label="基本要求">
                                <template slot-scope="scope">
                                    <el-input v-model="scope.row.basicRequire"  type="textarea" :rows="1" ></el-input>
                                </template>
                            </el-table-column>
                            <el-table-column label="优秀">
                                <template slot-scope="scope">
                                    <el-input v-model="scope.row.excellence"  type="textarea" :rows="1" ></el-input>
                                </template>
                            </el-table-column>
                            <el-table-column label="良好" width="200">
                                <template slot-scope="scope">
                                    <el-input v-model="scope.row.well"  type="textarea" :rows="1" ></el-input>
                                </template>
                            </el-table-column>
                            <el-table-column label="合格" width="200">
                                <template slot-scope="scope">
                                    <el-input v-model="scope.row.qualified"  type="textarea" :rows="1" ></el-input>
                                </template>
                            </el-table-column>
                            <el-table-column label="不合格" width="210">
                                <template slot-scope="scope">
                                    <el-input v-model="scope.row.disqualification"  type="textarea" :rows="1" ></el-input>
                                </template>
                            </el-table-column>
                            <el-table-column label="成绩比例（%）" width="210">
                                <template slot-scope="scope">
                                    <el-input-number v-model="scope.row.ratio"  :min="0" controls-position="right"></el-input-number>
                                </template>
                            </el-table-column>
                            <el-table-column label="操作" width="50">
                                <template slot-scope="scope">
                                    <label @click="rowDel(index, scope.$index)" class="tail-end_del"><SvgIcon icon-class="iconqudiao" size="20px"></SvgIcon></label>
                                </template>
                            </el-table-column>
                        </el-table>
                <p class="b-remark">{{`注：该表格中比例为${item.examine_name}成绩比例。`}}</p>
                <div @click="rowAdd(index)" class="add-step-title add-step-button"><SvgIcon icon-class="icontianjia" size="20px"></SvgIcon><span>添加基本要求</span></div>
            </el-row>
              </el-form-item>
            <el-row :gutter="24">
                <el-col :span="6" :offset="9">
                    <el-button @click="formSubmit('form', 'save')">保&nbsp;&nbsp;存</el-button>
                    <el-button type="primary" @click="formSubmit('form', 'next')">下一步</el-button>
                </el-col>
            </el-row>
       </el-form>
     </div>
 </div>
</template>

<script>
import TitleCurmb from './components/TitleCurmb'
import CourseSteps from './components/CourseSteps'

export default {
 name: 'EvaluationCriterion',
 components: {TitleCurmb, CourseSteps},
 data() {
   const checkTableData = (rule, value, callback) => {    
        const lengthNum=this.tableData.length
        let empty=0
        let cycleIndex=0
        let num=0
        this.tableData.forEach(item =>{   
             if(item.requires.length ===0){
                    empty++
                }else{
                    cycleIndex ++
                }
            item.requires.forEach(vo =>{      
                if(vo.basicRequire === '' || vo.excellence === '' || vo.well === '' || vo.qualified === '' ||  vo.disqualification === '' || vo.ratio === ''|| vo.ratio === 0){
                        num ++
                    }
                })
            if(empty!==0){
                 callback(new Error(`请添加考核与评价标准`))
            }

            if(cycleIndex ===lengthNum && num ===0){
                callback()
            }else if(num !==0){
                callback(new Error(`考核与评价标准内容不能有空`))
            }
        })
    };
 return {
    courseName: this.$route.query.title,
    tableData:[],
    form:{
      tableData:[]
    },
    rules:{
        tableData: [{ required: true, validator: checkTableData, trigger: "blur"}],
    },
    api:{
         details:courseId=>`/basic/course/setExamPattenRequiedView/${courseId}`, //详情
         save:() => `/basic/course/setExamPattenRequied`
    }
 }
 },
 created(){},
 mounted(){
     this.initData()
 },
 methods:{
     initData(){
        const url=this.api.details(this.$route.query.id)
        this.$get(url).then(res =>{
            if(res.status ===1){    
                this.tableData=res.data || []
                
            }else{
                this.$notify.error({
                    title: "error",
                    duration: 1500,
                    message: res.message
                });
            }
        })
     },
    rowAdd(index){
      const obj=  {
            basicRequire:'',
            excellence:'',
            well:'',
            disqualification:'',
            qualified:'',
            ratio:0
        }
        this.tableData[index].requires.push(obj)
    },
    rowDel(parentIndex, index){
        this.tableData[parentIndex].requires.splice(index, 1)   
    },
    formSubmit(formName, type){
         if(type === 'save'){
             this.$refs[formName].resetFields()
              this.userSave('save')
         }else{ 
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.userSave('next')
                }
            });
         }
       
     },
    userSave(type){
        const examineArr= this.tableData.map(item =>{
             item.requires.forEach(vo =>{
                 if(!vo.examId){
                     vo.examId=item.examine_id
                 }      
             })
             return {
                 requireList:item.requires
             }
        })
       const postData={
            id:this.$route.query.id,
            examineList:examineArr  
        }

        this.$post(this.api.save(), postData).then( res => {
          if(res.status === 1){
              this.$notify.success({
                  title: 'success',
                  duration:1500,
                  message: res.message
                  })
              if(type === 'next'){
                    this.$router.push({
                      path:'/Construction/reference',
                      query:{
                          id:this.$route.query.id,
                          title:this.courseName
                      }
                  })
              }else{
                  this.initData()
              } 
          }else{
              this.$notify.error({
                  title: 'error',
                  duration:1500,
                  message: res.message
              })
          }  
      })
     }
 }
}
</script>

<style lang='scss' scoped>
 @import '../../styles/courseConstruction.scss';
</style>



